@import url(https://fonts.googleapis.com/css?family=Lato);
@import url(https://fonts.googleapis.com/css?family=Lato);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
}

* {
  box-sizing: border-box;
}

body {
  font-family: "Lato", sans-serif;
}

h1 {
  letter-spacing: 2px;
  text-align: center;
  text-transform: uppercase;
  margin-left: 1rem;
  margin-right: 1rem;
}

.countdown-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  position: absolute;
  top: 25%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.countdown-item {
  color: #fff;
  font-size: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 10px;
  padding-top: 10px;
  margin-left: 5rem;
  margin-right: 5rem;
  position: relative;
  width: 100px;
  height: 100px;
}

.Header_title {
  color: azure;
}
.headerMenu {
  margin: 0 10px;
  text-decoration: none;
  color: #565673;
  font-size: 16px;
  font-weight: 200;
  letter-spacing: 1px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  display: table-row;
  flex-direction: row;
}
.headerMenu:hover {
  color: #5f9ea0;
  text-shadow: 0.5px 0.5px green;
}
.seeYouSoon {
  color: white;
  font-family: "Lato", sans-serif;
  letter-spacing: 2px;
  text-align: center;
  font-size: 35px;
  position: absolute;
  top: 35%;
  margin-left: 50%;
  transform: translate(-50%, -50%);
  text-transform: uppercase;
}

